import React from 'react';
import NewsCard from '../NewsCard'
import './News.scss'

import { IconButton, Grid, CircularProgress } from '@material-ui/core';
import { KeyboardArrowDown, ErrorOutline } from '@material-ui/icons';

function NewsPresenation(props) {

    const { loading, error, articles, loadMore, hasNextPage } = props;
    
    return (
        
        <div className="news-flex">
            {articles.map((article) =>
                (
                    <NewsCard key= {article.id} {...article} />
                )
            )}
            <Grid container justify="center">
                {loading ? (<CircularProgress />)
                : (<IconButton color="primary"  aria-label="upload picture" component="span"
                        onClick={() => hasNextPage && !loading ? loadMore() : undefined}>
                    
                            {error ?  (<ErrorOutline fontSize="large"  color="secondary"/>)
                            :(<KeyboardArrowDown fontSize="large"/>)}
                        
                    </IconButton>)
                }
            </Grid>
        </div>

    )
}

export default NewsPresenation

