import React from 'react';
import moment from 'moment'
import { faClock, faNewspaper} from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

function toSnippet(content, maxLength) {
    return content.length >= maxLength
        ? content.substring(0, maxLength - 3) + "..."
        : content;
}

const openInNewTab = (url, event) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
    if (event) event.stopPropagation();
}

function NewsCard(props) {
    const isOnlyText = !props.imageUrl;
    const articleDate = new Date(props.pubDateTime).toString();
    const readableDate = moment(new Date(articleDate)).format('LLLL');
    const relativeDate = moment(new Date(articleDate)).fromNow();
    return (
        <div

            onClick={() => openInNewTab(props.articleUrl)}
            className="clickable news-card-flex shadowed">
            <div className="news-card-column alternative">

                {getImage(props.imageUrl, props.title)}
                {getTitle(props.title, props.link)}
                <div className="content-snippet">{toSnippet(props.articleSummary, (isOnlyText ? 250 : 100))}</div>
                {getSourceTitleWithTime(props.sourceTitle, props.sourceUrl, readableDate, relativeDate )}
                
            </div>

            <div className="news-card-row main">
                <div className="news-content">
                    {getTitle(props.title, props.articleUrl)}
                    {getTimeDiv(readableDate, relativeDate)}
                    <div className="content-snippet">{toSnippet(props.articleSummary, (isOnlyText ? 400 : 200))}</div>
                    {getSourceTitle(props.sourceTitle, props.sourceUrl)}

                </div>
                {getImage(props.imageUrl, props.title)}
            </div>
        </div>
    )
}

function getSourceTitleWithTime(sourceTitle, sourceUrl, readableDate, relativeDate) {

    return (
        <div  className="article-footer">
            <FontAwesomeIcon icon={faNewspaper} /> 
            <span className="source" onClick={(event) => openInNewTab(sourceUrl, event)}>{sourceTitle}</span>
            <span>-</span>
            <span title={readableDate}> {relativeDate}</span>
        </div>
    )
}
function getSourceTitle(sourceTitle, sourceUrl) {

    return (
        <div className="article-footer">
            <FontAwesomeIcon  icon={faNewspaper} />
            <a className="source" href={sourceUrl} onClick={(e) => e.stopPropagation()} target="_blank" rel="noreferrer noopener">
                {sourceTitle}
            </a>
        </div>
    )
}
function getTimeDiv(readableDate, relativeDate) {

    return (
        <div title={readableDate} className="article-header">
            <span><FontAwesomeIcon icon={faClock} /> {relativeDate}</span>
        </div>
    )
}

function getTitle(title, link) {

    return (
        <div className="article-title">
            <a href={link} onClick={(e) => e.stopPropagation()} target="_blank" rel="noreferrer noopener">
                {title}
            </a>
        </div>
    )
}

function getImage(imgLink, title) {
    console.log(imgLink)
    if (imgLink) {
        return (
            <div className="news-img-container">
                <img className="news-img" src={imgLink} alt={title} referrerPolicy="no-referrer" />
            </div>
        )
    } else {
        return;
    }
}

export default React.memo(NewsCard)
