import React from 'react';

import { useQuery, gql } from '@apollo/client';
import NewsPresenation from './NewsPresentation';

const GET_ARTICLES = gql`query Articles($cursor: String) {articles(first: 10, after: $cursor) {
    edges {
      node {
        id, pubDateTime, title, sourceTitle, sourceUrl,
        articleUrl,  articleSummary, imageUrl
      }
      cursor
    } 
    pageInfo {startCursor, endCursor, hasNextPage}
    totalCount
  }}`;

function toArticles(articles) {
    return articles?.edges?.map( x=> x.node) || [];
}
function hasNextPage(articles) {
    return articles?.pageInfo?.hasNextPage;
}

function News() {

    const { loading, error, data, fetchMore } = useQuery(GET_ARTICLES);
    
    function loadMore () {
        fetchMore({
            variables: {
              cursor: data?.articles?.pageInfo?.endCursor
            }
        });
    }
    
    return (
        <NewsPresenation 
            loading = {loading} error = {error} articles= {toArticles(data?.articles)} loadMore={loadMore} hasNextPage={hasNextPage(data?.articles)}
        />
    )
}

export default News

